import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学校视角-学生情况-违纪情况.json'
export default class Weijiqingkuang extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        学生违纪情况（%）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                    {/* <div className='item-info'>
                        当前值：5.2/生，比目标值高23%
                                </div> */}
                </div>
            </div>
        )
    }
}
