import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Carousel} from 'antd'
import { carouselTitleAction } from './../../redux/actions/carouselTitleAction';
import { majorMenuAction } from './../../redux/actions/majorMenuAction'
import { isShowMajorMenuAction } from './../../redux/actions/isShowMajorMenuAction'

// 学校
import Biyelv from './component/school/Biyelv'
import Biyesheji from './component/school/Biyesheji'
import Dinggaoshixi from './component/school/Dinggaoshixi'
import Jiuyelv from './component/school/Jiuyelv'
import Ruzhibannianmanyidu from './component/school/Ruzhibannianmanyidu'
import Shengjunyiqi from './component/school/Shengjunyiqi'
import Shuangshisuzijiaoshi from './component/school/Shuangshisuzijiaoshi'
import Wenmingbanji from './component/school/Wenmingbanji'
import Wenmingsushe from './component/school/Wenmingsushe'
import Zhuanyezongxueshi from './component/school/Zhuanyezongxueshi'
import Shengshibi from './component/school/Shengshibi'
import Waipinjiaoshizhanbi from './component/school/Waipinjiaoshizhanbi'
import Weijiqingkuang from './component/school/Weijiqingkuang'

// 专业
import Major from './component/major'


class Yujing extends Component {
  render() {
    let autoplaySpeed = 40000;
        const settings = {
            // autoplay: true,
            autoplaySpeed,
            dots: true,
            infinite: false,
            slidesToShow: 1,
            slidesToScroll: 1,
            vertical: true,
            verticalSwiping: true,
            beforeChange: (currentSlide, nextSlide) => {
                this.props.changeCarouselTitle(nextSlide);
                if (nextSlide === 1){
                    this.props.filterMajor("1");
                    this.props.toggleMajorMenu(true);
                }else {
                    this.props.toggleMajorMenu(false);
                }
            },
            afterChange: (currentSlide) => {
                if (currentSlide === 4) {
                    setTimeout(() => {
                        if (this.slider && this.slider.goTo) {
                            this.slider.goTo(0)
                        }
                    }, autoplaySpeed)
                }
            },
        };
    return (
      <Carousel  ref={slider => (this.slider = slider)} {...settings} >
      {/* 学校 */}
        <div>
            <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
                <Biyelv></Biyelv>
                <Biyesheji></Biyesheji>
                <Dinggaoshixi></Dinggaoshixi>
                <Jiuyelv></Jiuyelv>
                <Ruzhibannianmanyidu></Ruzhibannianmanyidu>
                <Shengjunyiqi></Shengjunyiqi>
                <Shuangshisuzijiaoshi></Shuangshisuzijiaoshi>
                <Wenmingbanji></Wenmingbanji>
                <Wenmingsushe></Wenmingsushe>
                <Zhuanyezongxueshi></Zhuanyezongxueshi>
                <Shengshibi></Shengshibi>
                <Waipinjiaoshizhanbi></Waipinjiaoshizhanbi>
                <Weijiqingkuang></Weijiqingkuang>
            </div>
        </div>
        {/* 专业 */}
        <div>
            <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
                <Major></Major>
            </div>
        </div>
      </Carousel>
    )
  }
}
export default connect(null, 
    (dispatch) => {
        return {
            changeCarouselTitle(type){
                const action = carouselTitleAction(type);
                dispatch(action);
            },
            filterMajor(type){
                const action = majorMenuAction(type);
                dispatch(action);
            },
            toggleMajorMenu(bool){
                const action = isShowMajorMenuAction(bool);
                dispatch(action);
            }
        }
    })(Yujing)




