import React, { Component } from 'react'

import Ruzhibannianmanyidu from './Ruzhibannianmanyidu'
import Shengjunyiqi from './Shengjunyiqi'

export default class Kuaiji extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'kuaiji' || url === 'all')?'block':'none'} }>
                <Ruzhibannianmanyidu></Ruzhibannianmanyidu>
                <Shengjunyiqi></Shengjunyiqi>
            </div>
        )
    }
}

