import React, { Component } from 'react'

import Shuangshisuzhi from './Shuangshisuzhi'

export default class Jisuanji extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'qicheyunyong' || url === 'all')?'block':'none'} }>
                <Shuangshisuzhi></Shuangshisuzhi>
            </div>
        )
    }
}

