import React, { Component } from 'react'

import Jiuyelv from './Jiuyelv'
import Lilunxueshishu from './Lilunxueshishu'

export default class Jisuanji extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'shukong' || url === 'all')?'block':'none'} }>
                <Jiuyelv></Jiuyelv>
                <Lilunxueshishu></Lilunxueshishu>
            </div>
        )
    }
}

