import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/major/qicheyunyong/双师素质教师（%）.json'
export default class Shuangshisuzhi extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        双师素质教师（%）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                    {/* <div className='item-info'>
                        当前值：5.2/生，比目标值高23%
                                </div> */}
                </div>
            </div>
        )
    }
}
